import React ,{useEffect, useState} from 'react';
import {useLocation} from 'react-router-dom'
import { Button, FlexboxGrid} from 'rsuite';
import Total from '../Components/Total';
import { useValue } from '../Misc/ContextProvider';

const Devices = () => {
  
  const [buylist,updateList] = useState([])
  const [cartstate,updatecartstate] = useState(0)
  const [update,updatechange] = useState(0)
  const location = useLocation()
  const search = location.search.slice(1)
  const {products,managecart} = useValue()

  console.log(products,location)
  //FUNCTION TO UPDATE THE BUYLIST
  function addtocart(ev){
    managecart(ev.target.id)
  }
    
    
  return (<div className='mt-20'>
    <Total buylist = {buylist}/>
      <FlexboxGrid justify="space-around">
        {
          products.map((value)=>{
            if (value.categoryId === search){

              return (<FlexboxGrid.Item colspan={2} key={value.id} >
                <div className='card-design'>
                <img className='imgcat bord' src={value.thumbnail} alt={value.name}></img>
               <h6> name:{value.name}</h6>
               <p>${value.price}</p>
               <p className={value.inStock?"available":"notavailable"}>{value.inStock?"In stock":"Out of stock"}</p>
               <Button 
               onClick={addtocart} 
                disabled={value.inStock?false:true} 
                id={value.id} block>
                  Add to cart
                  </Button>

                  </div>
                </FlexboxGrid.Item>)
                
            }
            
             
          })
        }
      
    </FlexboxGrid>
    
  </div>)
};

export default Devices;
