import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Button } from 'rsuite';

const Empty = () => {
    let navigate = useNavigate()
    const returntoHome = ()=>{
        navigate('/')

    }
  return <div>
      CART IS empty
      <Button onClick={returntoHome}>HOME</Button>
  </div>;
};

export default Empty;
