import React, { createContext, useContext,useState, useEffect, useReducer } from 'react';
import categories from '../Database/categories.json'
import Products from '../Database/products.json'

const ACTION = {
    ADDNEW:'addnew',
    ADDNEXT:'addnext',
    INCREASENUMBER:'increasenum',
}


const contextMain = createContext()

export const Contextwrapper = ({children}) => {
    const category = [...categories]
    const products = [...Products]
    function reducer(state, action) {
        switch (action.type) {
          case 'NEW':
              console.log("first add")
              return [action.value]
    
          case 'NEXT':
              console.log("value")
              return [...state,action.value]
            
          case 'INC':
                console.log("at increment")
                return  action.value
          
    
          default:
            //   throw new Error();
        }
      }






    const initializer = [{
        id:null,
        name:null,
        price:null,
        quantity:null,
        img:null
    }]
    const [increment,inchappen] = useState(0)
    const [currentcart,cartfun] = useReducer(reducer,initializer)
    const managecart = (productid)=>{
        // console.log("first read",currentcart.length)

        if(currentcart[0].id === null){
            for(let obj in products){
              if(products[obj].id === productid){
                //when cart is empty
                  let temp = products[obj]
                  cartfun(
                      {type:'NEW',
                    value:
                    {id:temp.id,
                      name:temp.name,
                    price:temp.price,
                    quantity:1,
                    img:temp.thumbnail
                  }})
                }
          }
          console.log("type",typeof(currentcart))
      
        }
        else{
            let flag = false
            for(let currentProducts in currentcart){
                //when the item is already present
                if(currentcart[currentProducts].id === productid){
                  let tempbuylist = currentcart
                  console.log(tempbuylist)
                  tempbuylist[currentProducts].quantity = tempbuylist[currentProducts].quantity + 1
                  console.log("from increment",tempbuylist)
                  cartfun({type:'INC', value:tempbuylist})
                // cartfun(
                //     {type:ACTION.ADDNEW,
                //   value:
                //   {id:tempbuylist.id,
                //   price:tempbuylist.price,
                //   quantity:1
                // }})
                  console.log("after increment",currentcart)
                  inchappen(!increment)
                  flag = false
                  break
                }
                else{
                    flag = true
                }
                }
            if(flag){
                console.log("from the new add to old")
                for(let obj in products){
                    if(products[obj].id === productid)
                    {
                      console.log("sending to ",products[obj])
                      cartfun({type:'NEXT',value:{
                          id:products[obj].id,
                          name:products[obj].name,
                          price:products[obj].price,
                          quantity:1,
                          img:products[obj].thumbnail

                      }})
                      console.log("after sending",currentcart)
                    break
                    }  
                  }
            }
        }
      }
    //   useEffect(()=>{
    //       console.log("hello",currentcart)
    //   },[currentcart])

    



    return(
        <contextMain.Provider value = {{products,category,managecart,currentcart,increment}}>
            {console.log(currentcart)}
            {children}
        </contextMain.Provider>
    )

}

export const useValue = ()=> useContext(contextMain)