import React, { useEffect } from 'react';
import { useValue } from '../Misc/ContextProvider';
import { Grid,Row,Col, Button } from 'rsuite';

const Cart = ({changehappen,makchange}) => {
    const {currentcart} = useValue()
    
    const dec = (ev)=>{
        let id = ev.target.id
        for(let obj in currentcart){
            if(currentcart[obj].id === id){
                currentcart[obj].quantity = currentcart[obj].quantity - 1
                makchange(!changehappen)
                break
            }
        }  
    }
    const inc = (ev)=>{
        let id = ev.target.id
        for(let obj in currentcart){
            if(currentcart[obj].id === id){
                currentcart[obj].quantity = currentcart[obj].quantity + 1
                makchange(!changehappen)
                break
            }
        }
    }

    const del = (ev)=>{
        let id = ev.target.id
        for(let obj in currentcart){
            if(currentcart[obj].id === id){
                console.log("delete happened first")
                if(currentcart.length > 1){
                    currentcart.splice(obj,1)
                }
                else{
                    currentcart[0] = {
                            id:null,
                            name:null,
                            price:null,
                            quantity:null,
                            img:null
                    }
                }
                

                console.log("delete happened")
                makchange(!changehappen)
                break
            }
        }
    }

    
  return <div>
      <Grid fluid>
                <Row gutter={16}>
                <Col xs={4}>
                    <div className="show-col"></div>
                </Col>
                <Col xs={4}>
                    <div className="show-col"><h5>NAME</h5></div>
                </Col>
                <Col xs={4}>
                    <div className="show-col"><h5>QUANTITY</h5></div>
                </Col>
                <Col xs={4}>
                    <div className="show-col"><h5>TOTAL AMOUNT</h5></div>
                </Col>
                <Col xs={4}>
                    <div className="show-col"><h5>REMOVE</h5></div>
                </Col>
                </Row>
                </Grid>
                <hr className='hr-cart'/>
      {
          currentcart.map((value)=>{
            return <Grid fluid key = {value.id}>
            <Row gutter={16} >
            <Col xs={4}>
                <div className="show-col"><img className='checkout-thumbnail' src={value.img} alt={value.name}/></div>
            </Col>
            <Col xs={4}>
                <div className="show-col"><h4>{value.name}</h4></div>
            </Col>
            <Col xs={4}>
                <div className="show-col"><h4>
                     <Button id={value.id} onClick={dec} disabled={value.quantity > 1?false:true}>-</Button>
                     &nbsp;{value.quantity} &nbsp;
                    <Button id={value.id} onClick={inc}>+</Button></h4></div>
            </Col>
            <Col xs={4}>
                <div className="show-col"><h4>{value.quantity * value.price}</h4></div>
            </Col>
            <Col xs={4}>
                <div className="show-col"><Button id={value.id} onClick={del}>X</Button></div>
            </Col>
            </Row>
            </Grid>
          })

      }
  </div>;
};

export default Cart;
